.App {
  text-align: center;
}

.Dollar-sign {
  height: 10vmin;
  margin-top: 20px;
  pointer-events: none;
}

.Betting-img {
  height: 50vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Dollar-sign {
    animation: Dollar-sign-spin infinite 2s linear;
    animation-delay: 0ms;
  }
}

.App-header {
  background-color: #ffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Greet {
  color: black;
  font-family: "Courier New", Courier, monospace;
  font-weight: bolder;
}

.App-link {
  color: #61dafb;
}

@keyframes Dollar-sign-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}
